@charset "UTF-8";@import url(http://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900|Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,800,300|Open+Sans+Condensed:300,700,300italic|Merriweather:400,900italic,700italic,400italic,300italic,900,700,300);@font-face{font-family:"Roboto-Condensed-Regular";src:url("../assets/css/fonts/RobotoCondensed-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Regular";src:url("../assets/css/fonts/Roboto-Regular.ttf") format("truetype")}@font-face{font-family:"Roboto-Light-Italic";src:url("../assets/css/fonts/Roboto-LightItalic.ttf") format("truetype")}.anim-fast{-webkit-transition:all .3s ease;transition:all .3s ease}.all-transitions{-webkit-transition:all .5s ease-out;transition:all .5s ease-out}.main_shadow{box-shadow:0 0 5px 2px rgba(0,0,0,0.22)}#panels #dashboard{background:tomato;color:black;height:500px}#dashboard .content .widget{border:0 solid black;color:#2E2E2E;font-family:Roboto-Condensed-Regular,verdana,sans-serif;box-shadow:none;outline:none;overflow:hidden;margin:5px 0;padding:2px 5px;border-radius:2px;font-size:1.2em}#dashboard .content .card{background:white;padding:5px;border-radius:2px;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column}#dashboard .content .card *{-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1}#dashboard .content .text_input,#dashboard .content .slider{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center}#dashboard .content .text_input input{border:0;background:transparent;border-bottom:2px solid white;color:white;-webkit-box-flex:2;-webkit-flex:2;-ms-flex:2;flex:2;min-width:20px;margin:0 10px}#dashboard .content .text_input button{width:50px}#dashboard .content .slider input[type=range]{-webkit-appearance:none;-webkit-box-flex:2;-webkit-flex:2;-ms-flex:2;flex:2}#dashboard .content .slider input[type="range"]::-moz-range-thumb{background-color:#ffbb33;width:10px;height:30px}#dashboard .content button{background-color:#f5d328;outline:none;border:none}#dashboard .content button:hover{background:#fae789}#dashboard .content button:active{background:#e0bc0a}#dashboard .content .html{margin:0;padding:0}#dashboard .content .html iframe{width:100%}


